<template>
  <div class="parent-dash">
    <div class="yellow-box">
      <div class="top-item">
        <p class="quote">Consistency is the key!</p>
        <img class="ussain-bolt" src="@/assets/ussain.png" />
      </div>
      <div class="second-card">
        <p class="date-day">{{date}}</p>
        <span class="update-icon">
          <dateday @date="updateDate($event)"/>
        </span>
        <span class="refresh-icon">
          <!-- <b-icon icon="arrow-clockwise" scale="2" ></b-icon> -->
          <refresh />
        </span>
       <p class="text-percent">{{percnt()}}</p>
        <div class="progressbar"><b-progress :value="value"  show-progress animated></b-progress></div>
        
      </div>
      <div class="check-box-section">
        <div class="inner-check-wrapper">
          <session @percentage="updatePercent(0,$event)" />
          <session @percentage="updatePercent(1,$event)" />
          <session @percentage="updatePercent(2,$event)"/>
          <session @percentage="updatePercent(3,$event)"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import session from "@/components/widgets/sessionBox.vue";
import refresh from "@/components/widgets/refresh.vue";
import dateday from "@/components/widgets/dateday.vue";
export default {
  components: {
    session,
    refresh,
    dateday
  },
  methods: {
  
    updatePercent(n,event){
        console.log("from parent ",n,event)
        this.session[n] = event;
        this.value = this.session[0] + this.session[1] + this.session[2] + this.session[3];
    },
    updateDate(event){
       this.date = event;
    },
    percnt(){
        if(this.value)
        return this.value.toFixed(2) + "%";
        else
        return "0%";
    }
  },
  data() {
    return {
        value:null,
        session: [0, 0, 0, 0],
        date:'DATE'
    };
  }
};
</script>
<style scoped>
.text-percent{
    position:absolute;
    font-size:30px;
    bottom:-0.6rem;
}
.inner-check-wrapper {
  margin: 0 auto;
  width: fit-content;
}
.check-box-section {
  min-height: 18rem;
  background-color: transparent;
  width: 80%;
  margin: 0 auto;
  margin-top: 1rem;
}
.parent-dash {
  padding: 2rem 9rem;
}
.yellow-box {
  background-color: yellow;
  min-height: fit-content;
  min-width: 40vw;
  width: 58rem;
  border-radius: 7px;
  margin: 0 auto;
}
.second-card {
  position: relative;
  background-color: white;
  width: 44rem;
  margin: 0 auto;
  min-height: 2rem;
  border-radius: 4px;
  padding: 1rem;
}
.quote {
  display: inline;
  margin-right: 4rem;
  padding-left: 4rem;
  font-size: 18px;
  font-weight: 500;
  position: relative;
  bottom: -5rem;
}
.top-item {
  padding-left: 4rem;
  margin-bottom: 10px;
}
.ussain-bolt {
  width: 14rem;
  position: relative;
  bottom: -10px;
}
.date-day {
  font-size: 30px;
}
.update-icon {
  position: absolute;
  top: 15px;
  right: 7.2rem;
  cursor: pointer;
}
.refresh-icon {
  position: absolute;
  top: 15px;
  right: 2rem;
  cursor: pointer;
}
.update-icon:hover {
  color: yellow;
}
.refresh-icon:hover {
  color: yellow;}
.progressbar{
position:relative;
left:7rem;
width:83%;
}
</style>