import Vue from 'vue'
import Router from 'vue-router'
import login from "@/components/pages/login"
import example from "@/components/pages/example"
import dashboard from "@/components/pages/dashboard"
Vue.use(Router)
export default new Router({
    mode: "history",
    routes: [
        {
            path: "/",
            name: "login",
            component: login
        },
        {
            path: "/example",
            name: "example",
            component: example
        },
        {
            path: "/dashboard",
            name: "dashboard",
            component: dashboard
        }
    ]
})