<template>
  <v-row justify="center" data-app >
    <v-dialog v-model="dialog" persistent max-width="290">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="primary"
          dark
          v-bind="attrs"
          v-on="on"
        >
         <b-icon icon="arrow-clockwise" scale="2" ></b-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title class="headline">Refresh the data</v-card-title>
        <v-card-text>All your work track will get deleted if you agree. Are you sure to do this?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">Disagree</v-btn>
          <v-btn color="green darken-1" text @click="dialog = false; reload()">Agree</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
      }
    },
    methods:{
        reload(){
            this.$router.push({path:"/"})
            // location.reload();
        }
    }
  }
</script>