<template>
  <div class="session-box">
    <div v-for="items in 4" :key="items">
      <span class="big-box">
        <p-check v-model="state[items-1]" class="p-svg p-plain p-jelly">
          <img
            slot="extra"
            class="svg"
            src="https://hamed-ehtesham.github.io/pretty-checkbox-vue/svg/open-iconic/task.svg"
          />
          <!-- {{state[items-1]}} -->
        </p-check>
      </span>
      <span class="small-box">
        <p-check v-model="rest[items-1]" class="p-icon p-round p-jelly" color="primary">
          <i slot="extra" class="icon mdi mdi-check"></i>
        </p-check>
      </span>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      state: [false, false, false, false],
      rest:[false,false,false,false]
    };
  },
  methods:{
    sendPercentage(){
        let sum = this.state[0]+this.state[1]+this.state[2]+this.state[3];
        let stateMinutes = sum*25;
        let rest = this.rest[0]+this.rest[1]+this.rest[2];
        let restMinutes = rest*5;
        let diamondRest = this.rest[3]*20;
        let percentage =  ((stateMinutes + restMinutes + diamondRest)/135) *25 ;
        this.$emit('percentage',percentage)
        console.log('percentage is '+percentage+"%");
        
    }
  },
  watch:{
      state:function(){
         this.sendPercentage();
      },
      rest:function(){
         this.sendPercentage();
      }
  }
};
</script>
<style scoped>
.session-box {
  display: inline-block;
  background-color: white;
  width: fit-content;
  padding: 1rem;
  margin: 0 1rem;
  border-radius: 4px;
}
.big-box {
  font-size: 2rem;
}
.small-box {
  font-size: 1.5rem;
}
</style>
 <style lang="scss">
@import "~pretty-checkbox/src/pretty-checkbox.scss";
</style>